import * as React from "react"
import { graphql, PageProps } from "gatsby"
import ShareButton from "../../components/share-button";

interface ArticleTemplateProps extends PageProps {
  data: {
    markdownRemark: {
      frontmatter: {
        title: string
        date: string
      }
      html: string
    }
  }
}

const ArticleTemplate: React.FC<ArticleTemplateProps> = ({ data }) => {
  const { markdownRemark } = data
  const { frontmatter, html } = markdownRemark

  return (
    <div>
      <div>
        <h1>{frontmatter.title}</h1>
        <h4>Wrote by AI - <a href=''>Report</a><ShareButton
        title="Shared HTML Page"
        text="Check out this HTML page!"
        url={'https://1241am.com/articles/fusin-franco-coreana-una-nueva-armona-repostera/'}
      /></h4>
        <h5>{frontmatter.date}</h5>
        <div dangerouslySetInnerHTML={{ __html: html }} />
      </div>
    </div>
  )
}

export const pageQuery = graphql`
  query($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        slug
        title
      }
    }
  }
`

export default ArticleTemplate
